class IntroSection extends React.Component {
  constructor(props){
    super(props);
    this.changeLanguage = this.changeLanguage.bind(this);
  }
  changeLanguage (event){
    if (i18n.language== 'es')
      i18n.changeLanguage('en');
    else
      i18n.changeLanguage('es');
    ReactDOM.render(
        <React.StrictMode>
          <App />
        </React.StrictMode>,
        document.getElementById('root')
      );
  }
  render() {
    return (<section id="intro">
        <div className="intro-overlay"></div>	
        <div className="intro-content">
          <div className="row">
            <div className="col-twelve">
            <h5>{i18n.t('Introduction')}</h5>
              <h1>I'm Esteban Hernández</h1>
              <p className="intro-position">
                <span>Software Developer</span>
              </p>
              <a className="button stroke smoothscroll" href="#about" title="">More About Me</a>
            </div>
          </div>
        </div>
        <ul className="intro-social">
            <li><a href="https://github.com/tebanhdez/tebanhdez"><i className="fa fa-github"></i></a></li>
            <li><a href="https://www.linkedin.com/in/estebanhernandezhernandez/?locale=en_US"><i className="fa fa-linkedin"></i></a></li>
            <li><a href="https://twitter.com/tebanHdez"><i className="fa fa-twitter"></i></a></li>
        </ul>
     </section>
    );
  }
}

