class Loader extends React.Component {
  render() {
    return (
          <div id="preloader"> 
            <div id="loader"></div>
          </div>
    );
  }
}
class App extends React.Component {
  constructor(props){
    super(props);
    this.downloadCV = this.downloadCV.bind(this);
  }

  downloadCV(event) {
    event.preventDefault();
    fetch('resume/download')
      .then(response => {
        response.blob().then(blob => {
          let url = window.URL.createObjectURL(blob);
          let a = document.createElement('a');
          a.href = url;
          a.download = 'EstebanHHcv.pdf';
          a.click();
        });
    });
  }
  render(){
    return (
      <div>
        <IntroSection />
        <AboutSection downloadCV={this.downloadCV}/>
        <ResumeSection />
        <Footer />
      </div>
    );
  }
}
export default App;
